
body{
  background: #111320;
  color: whitesmoke;
}

.resume-container,
.container-nav{
  width: 95%;
  max-width: 1000px;
  margin: 1em auto; /* centers on the page */

}

.resume-container{

  padding-top: 1em;

}

.container-nav {
  display: flex;
  z-index:100;
  justify-content: flex-end;
  border-bottom: 1px solid #ffcc66;
  background: #111320;
}

.nav-list {
  display: flex;


  width: 100%;
}

.navItem {
  margin: 0 1em;
}
.navItem:hover,
.navItem:focus{
  color: red;
}



.resume-side {
  width: 100%;
  margin: 0.5em;
  padding: 0.5em;
  text-align: center;
  border-radius: 0.5em;
  background: #1a1e32;

}

.resume-main {
  width: 100%;
  margin: 0.5em;
}

.resume-section-title,
.resume-side-name{
  color: #ffcc66;
  font-family: Calibri, serif;

}

.resume-side-name{
  font-size: 1.8em;
}
.resume-side-intro{

}

.resume-side-Title{
  font-size: 1em;
  font-family: monospace;
  margin: 1.2em 0;
}



.resume-main-section{
  margin: 5px;
  border-radius: 0.6em;
  background: #1a1e32;
  padding: 1.4em;
}
.resume-section-title{
  font-weight: bold;
  font-size: 1.5rem;
}

.resume-section-body{
  margin: 1em;
}


/* Skills */

.skill-section{
  margin: 1em 0;
}
.skill-section-title{

}

.skill-itemset{
  display: flex;
  justify-content: space-between;
}
